import React, { Component } from 'react';
import idx from 'idx';

import firebaseData from '../../scripts/firebaseData'

import { Link } from 'react-router-dom';

import listFactory from './listFactory';
import ContentItem from './ContentItem';
import ContentHeader from './ContentHeader';

const testHEADER = {
    header: {
        titleTop: 'Main Header',
        titleBot: 'test test'
    }
}


const testDATA = [{
    content: {

        topHeader: 'Main Header',
        botHeader: 'test test',
        text: 'witam'
    }
}, {
    content: {

        topHeader: 'Main Header',
        botHeader: 'test test',
        text: 'witam'
    }
}
]





class Home extends Component {

    state = {
        header: [],
        data: []
    }

    // https://snekui.firebaseio.com/test/docs/header

    // componentWillMount() {
    //     db(`content/docs/home/header`, snapshot => {
    //         let data = JSON.stringify(snapshot.val())
    //         data = JSON.parse(data);
    //         this.setState({ data: data })
    //     });
    // }

    componentWillMount() {
        firebaseData(`test/docs/header`, snapshot => {
            let data = JSON.stringify(snapshot.val())
            data = JSON.parse(data);
            this.setState({ header: data })
        });
        // this.setState({ header: testHEADER })
        this.setState({ data: testDATA })

    }

    render() {
        console.log('header ', this.state.header)
        console.log('data ', this.state.data)

        return (
            <>
                <ContentHeader
                    titleTop={this.state.header.titleTop}
                    titleBot={this.state.header.titleBot}
                />
                {/* <ul> */}
                < div >
                    {listFactory('', this.state.data, ContentItem, item => {
                        console.log(item, idx(item, _ => _.content))
                        return ({
                            topHeader: idx(item, _ => _.content.topHeader) || '',
                            botHeader: idx(item, _ => _.content.botHeader) || '',
                            // content: idx(item, _ => _.content.text) || ''
                        });
                    })}
                </div >

                {/* </ul> */}
            </>
        );
    }
}

export default Home;
