import React from 'react'
import styled from 'styled-components';

const Wrapper = styled.header`
    border-bottom: 1px solid #555555;
`

const Test = styled.span`
    position: absolute;
    right: 0;
    width: 0;
    background: #eeeeee41;
    top: 0;
    height: 100%;
    transition: .3s;

    span {
        font-size: 1.5em;
        display: none;
        overflow: hidden;
        left: -90px;
        color: #242424;
        transition-delay: 2s;
        transition: 2s;
    }
`

const Link = styled.a`
    position: relative;
    align-items: center;
    transition: .3s;
    color: #fff;
    padding-left: 15px;

    div {
        width: 23px;
        margin-right: 5px;
    }

    :hover {
        color: #ffffff00;
        overflow:hidden;
        background: #ddd;
        padding-left: 100px;

        .doc-logo {
            visibility: hidden;
            transition: .1s;
        }
        ${Test} {
            width: 100%;
            left: 0;

            span {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                height: 100%;
                width: 100%;
                z-index: 999;

                transition-delay: 2s;
                transition: 2s;
            }
        }
    }
`

function Header() {
    return (
        <Wrapper>
            <Link className="doc-flexbox" href="/">
                <div className="doc-logo"></div>
                <h3>Snek UI Docs</h3>
                <Test>
                    <span>homepage</span>
                </Test>
            </Link>
        </Wrapper>
    )
}

export default Header;