import React from 'react';


function listFactory(prefix, data = [], ItemComponent, customPropsCreator) {
    if (Array.isArray(data)) {
        return data.map((item, index) => {
            const customProps = (customPropsCreator && customPropsCreator(item)) || {};
            return (<ItemComponent
                key={`${prefix}-${item.id}-${index}`}
                {...customProps}
                {...item}
            />);
        });
    }

    return null;
}

export default listFactory